import '../css/styles.scss';
const { Configuration, OpenAIApi } = require("openai");
const apiKey = process.env.OPENAI_API_KEY;


const configuration = new Configuration({
  apiKey,
  basePath: "https://free.gpt.ge/v1"
});
const openai = new OpenAIApi(configuration);

// 取得使用者輸入的元素
const userInputElement = document.getElementById('insertBox');
// 取得要顯示回應的元素
const chatBoxElement = document.querySelector('.history');

async function getResponse() {
  const userMessage = userInputElement.value;

  try {
    const chatCompletion = await openai.createChatCompletion({
      model: "gpt-3.5-turbo",
      messages: [{ role: "user", content: userMessage }],
    });

    // 將回應顯示在 chatBox 中
    chatBoxElement.textContent = chatCompletion.data.choices[0].message.content;
    
  } catch (error) {
    console.error(error);
    // 處理錯誤，例如顯示錯誤訊息給使用者
  }
}

// 按鈕點擊事件
document.querySelector('.submit').addEventListener('click', async () => {
  getResponse();
});

// 按下 Enter 鍵事件
document.querySelector('.submit').addEventListener('keypress', (event) => {
  if (event.key === 'Enter') {
    event.preventDefault(); // 阻止預設行為 (避免換行)
    getResponse();
  }
});